// app/api/generate-description/route.js

export async function POST(req) {
  try {
    const { name } = await req.json();

    const response = await fetch('https://openrouter.ai/api/v1/chat/completions', {
      method: 'POST',
      headers: {
        'Authorization': `Bearer ${process.env.OPENROUTER_API_KEY}`,
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        model: 'openai/gpt-3.5-turbo',
        messages: [
          { role: 'system', content: 'You are an e-commerce copywriter.' },
          { role: 'user', content: `Write a short, vivid product description (max 50 words) for: ${name}` },
        ],
      }),
    });

    const data = await response.json();
    const description = data?.choices?.[0]?.message?.content?.trim();

    if (!description) throw new Error('No description returned');

    return new Response(JSON.stringify({ description }), {
      status: 200,
      headers: { 'Content-Type': 'application/json' },
    });
  } catch (err) {
    console.error('OpenRouter error:', err.message);
    return new Response(JSON.stringify({ error: err.message }), {
      status: 500,
      headers: { 'Content-Type': 'application/json' },
    });
  }
}
